.contact {
  position: relative;

  padding-bottom: 120px;
}

@media (max-width: 1024px) {
  .contact {
    padding-bottom: 0;
  }
}

.contact__container {
  display: grid;
  grid-template-columns: 1fr 715px;
  grid-column-gap: 10px;
  align-items: start;
}

@media (max-width: 1024px) {
  .contact__container {
    grid-template-columns: 1fr;
    grid-row-gap: 40px;
  }
}

.contact__content {
  display: grid;
  justify-items: start;
}

.contact__title {
  margin: 0;
  margin-bottom: 30px;

  font-weight: 400;
  font-size: 36px;
  font-family: var(--Ubuntu);
  line-height: 115%;
}

.contact__list {
  display: grid;
  grid-row-gap: 30px;

  margin-bottom: 30px;
}

@media (max-width: 1024px) {
  .contact__list {
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 16px;
    grid-column-gap: 20px;
    align-items: start;
  }
}

@media (max-width: 580px) {
  .contact__list {
    grid-template-columns: 1fr;
    grid-row-gap: 30px;
  }
}

.contact__item {
  display: grid;
  grid-row-gap: 12px;
  justify-items: start;
}

@media (max-width: 1024px) {
  .contact__item:nth-child(2) {
    grid-row: 1/3;
    grid-column: 2/3;
  }
}

@media (max-width: 580px) {
  .contact__item:nth-child(2) {
    grid-row-start: 2;
    grid-column: 1/-1;
  }
}

.contact__item h3 {
  margin: 0;
  margin-bottom: 4px;

  font-weight: 400;
  font-size: 20px;
  line-height: 115%;
}

.contact__item address {
  max-width: 300px;

  color: #433f38.8;

  font-style: normal;
  line-height: 125%;
}

.contact__item p {
  margin: 0;

  color: #433f38.8;

  line-height: 115%;
}

.contact__item a {
  color: #433f38;

  font-weight: 600;
  line-height: 115%;
  text-decoration: none;
}

.contact__item .contact__mail {
  color: #433f38.8;

  font-weight: 400;
}

.contact__popup {
  padding: 9px 23px;
}

.contact__map {
  position: relative;

  display: grid;

  height: 475px;
}

@media (max-width: 1024px) {
  .contact__map {
    justify-content: center;

    height: 400px;

    margin: 0 -20px;
  }
}

@media (max-width: 480px) {
  .contact__map {
    height: 320px;
  }
}

.contact__map::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;

  width: 47px;
  height: 47px;
  margin-top: 70px;
  margin-left: -18px;

  background-image: url("../img/map-pin.svg");
  background-repeat: no-repeat;

  transform: translate(-50%, -50%);
}

.contact__map::after {
  content: "";
  position: absolute;
  top: 110px;
  right: -200px;

  width: 335px;
  height: 212px;

  background-image: url("../img/contact-dc.png");
  background-repeat: no-repeat;

  transform: rotate(-149deg);
}

.contact__map img {
  position: absolute;
  left: 50%;

  object-fit: cover;

  min-width: 715px;
  border-radius: 6px;

  box-shadow: 0 0 14px rgba(90, 66, 4, .25);

  transform: translateX(-50%);
}

@media (max-width: 1024px) {
  .contact__map img {
    width: 100%;
    height: 400px;
  }
}

@media (max-width: 480px) {
  .contact__map img {
    height: 320px;
  }
}

.contact__yandex {
  position: relative;
  z-index: 2;

  width: 100%;
  max-width: 715px;
  height: 475px;
}

@media (max-width: 1024px) {
  .contact__yandex {
    min-width: 100vw;
    max-width: 100vw;
    height: 400px;
  }
}

@media (max-width: 480px) {
  .contact__yandex {
    height: 320px;
  }
}
