.consumers {
  position: relative;

  padding-bottom: 300px;

  background-color: #001824;
}

@media (max-width: 768px) {
  .consumers {
    padding-bottom: 150px;
  }
}

@media (max-width: 480px) {
  .consumers {
    padding-bottom: 60px;
  }
}

.consumers::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 1086px;

  background-image: url("../img/consumers-bg.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.consumers::after {
  content: "";
  position: absolute;
  right: -240px;
  bottom: 150px;

  width: 382px;
  height: 294px;

  background-image: url("../img/index-dc-04.png");
  background-repeat: no-repeat;

  transform: rotate(36deg);
}

@media (max-width: 1200px) {
  .consumers::after {
    display: none;
  }
}

.consumers__container {
  position: relative;

  display: grid;
}

.consumers__lead {
  justify-self: center;

  max-width: 854px;
  margin: 0;
  margin-bottom: 84px;

  color: #f8f1eb;

  font-size: 26px;
  font-family: var(--Ubuntu);
  line-height: 160%;
  text-align: center;
}

@media (max-width: 480px) {
  .consumers__lead {
    margin-bottom: 40px;

    font-size: 20px;
  }
}

.consumers__wrap {
  display: grid;
  grid-template-columns: 307px 1fr;
  align-items: start;
}

@media (max-width: 520px) {
  .consumers__wrap {
    grid-template-columns: 1fr;
  }
}

.consumers__list-wrap {
  display: grid;
}

.consumers__text {
  margin: 0;
  margin-bottom: 16px;

  color: #f8f1eb;

  font-weight: 500;
  font-style: normal;
  font-size: 26px;
  font-family: var(--Ubuntu);
  line-height: 180%;
}

.consumers__list {
  display: grid;
  grid-row-gap: 14px;

  margin: 0;
  padding: 0;

  list-style: none;
}

.consumers__item {
  position: relative;

  padding-left: 18px;

  color: #f8f1eb;

  font-size: 18px;
  line-height: 180%;
}

.consumers__item::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;

  width: 6px;
  height: 6px;
  border-radius: 50%;

  background: linear-gradient(180deg, #92c500 0%, #c8ee5d 100%);

  transform: translateY(-50%);
}

.consumers__image {
  position: relative;
}

@media (max-width: 680px) {
  .consumers__image {
    margin-left: -140px;
  }
}

@media (max-width: 520px) {
  .consumers__image {
    display: none;
  }
}

.consumers__image img {
  position: absolute;
  top: -60px;

  min-width: 1438px;

  transform: rotate(22deg);
}
