@keyframes nav-open {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 100%;
  }
}

@keyframes ellipce-big {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes ellipce-small {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(-360deg);
  }
}

@keyframes nav-link {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}

@keyframes preview-ellips {
  0% {
    top: 0;
    left: 0;

    width: 150px;
    height: 150px;

    border: 1px solid rgba(200, 238, 93, .9);
  }

  100% {
    top: -142px;
    left: -142px;

    width: 434px;
    height: 434px;

    border: 1px solid rgba(200, 238, 93, .05);
  }
}
