.seo {
  position: relative;
  z-index: 1;

  padding-bottom: 120px;
}

@media (max-width: 480px) {
  .seo {
    padding-bottom: 60px;
  }
}

.seo::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;

  width: 168px;
  height: 185px;

  background-image: url("../img/index-dc-06.png");
  background-repeat: no-repeat;
}

@media (max-width: 1600px) {
  .seo::before {
    display: none;
  }
}

.seo__container {
  display: grid;
  grid-template-columns: 1fr minmax(320px, 1fr);
  grid-column-gap: 10px;
  align-items: start;
}

@media (max-width: 768px) {
  .seo__container {
    grid-template-columns: 1fr;
  }
}

.seo__content {
  display: grid;
}

@media (max-width: 768px) {
  .seo__content {
    margin-bottom: 40px;
  }
}

.seo__title {
  margin: 0;
  margin-bottom: 30px;

  font-weight: 500;
  font-size: 24px;
  font-family: var(--Ubuntu);
  line-height: 120%;
}

.seo__text {
  display: grid;
  grid-row-gap: 28px;

  margin-bottom: 20px;
}

.seo__text--visible {
  overflow: visible;

  height: 100%;
}

.seo__text--hidden {
  overflow: hidden;

  height: 196px;
}

.seo__text p {
  margin: 0;

  font-size: 16px;
  line-height: 28px;
}

.seo__button {
  justify-self: start;

  padding: 0;
  border: none;

  background-color: transparent;
  color: #433f38.7;

  font-size: 16px;
  line-height: 180%;

  cursor: pointer;

  transition: .2s;
}

.seo__button:hover,
.seo__button:focus {
  color: #433f38;
}

.seo__image {
  display: grid;
  justify-content: end;
}

@media (max-width: 768px) {
  .seo__image {
    justify-content: center;
  }
}

.seo__image img {
  object-fit: cover;

  border-radius: 6px;
}
