.place {
  position: relative;

  padding-bottom: 120px;

  background-color: #001824;
}

@media (max-width: 480px) {
  .place {
    padding-bottom: 60px;
  }
}

.place::before {
  content: "";
  position: absolute;
  top: 102px;
  left: 0;

  width: 100%;
  height: 580px;

  background-image: url("../img/place-bg.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

@media (max-width: 1024px) {
  .place::before {
    top: 152px;
  }
}

@media (max-width: 480px) {
  .place::before {
    top: 202px;
  }
}

.place::after {
  content: "";
  position: absolute;
  top: -258px;
  left: 0;

  width: 100%;
  height: 360px;

  background-image: url("../img/place-dc-top.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

@media (max-width: 1024px) {
  .place::after {
    top: -208px;
  }
}

@media (max-width: 480px) {
  .place::after {
    top: -158px;
  }
}

.place__dc::before {
  content: "";
  position: absolute;
  top: 80px;
  left: 60px;
  z-index: 1;

  width: 223px;
  height: 231px;

  background-image: url("../img/place-dc-left.png");
  background-repeat: no-repeat;
}

@media (max-width: 1760px) {
  .place__dc::before {
    display: none;
  }
}

.place__container {
  position: relative;
  z-index: 1;

  display: grid;
}

.place__wrap-top {
  display: grid;
  grid-template-columns: 375px minmax(280px, 592px);
  grid-column-gap: 40px;
  align-items: center;
  justify-content: space-between;

  padding-bottom: 146px;
}

@media (max-width: 1024px) {
  .place__wrap-top {
    grid-template-columns: 1fr;
    grid-row-gap: 40px;

    padding-bottom: 40px;
  }
}

.place__content-top {
  display: grid;
  grid-row-start: 1;
  grid-column-start: 2;
  grid-row-gap: 20px;
}

@media (max-width: 1024px) {
  .place__content-top {
    grid-row-start: 1;
    grid-column: 1/-1;
  }
}

.place__content-top h2 {
  margin: 0;
  margin-bottom: 10px;

  color: #f8f1eb;

  font-weight: 500;
  font-size: 36px;
  font-family: var(--Ubuntu);
  line-height: 120%;
}

.place__content-top p {
  margin: 0;

  color: #f8f1eb;

  line-height: 180%;
}

.place__content-top b {
  font-weight: 600;
}

.place__text-wrap {
  position: relative;

  grid-row-start: 1;
  grid-column-start: 1;

  padding: 12px 8px;
  border-radius: 6px;

  background: #fcfcfc;
  box-shadow: 0 0 14px rgba(0, 0, 0, .25);
}

@media (max-width: 1024px) {
  .place__text-wrap {
    grid-row-start: 2;
    grid-column: 1/-1;
    justify-self: start;
  }
}

.place__decore {
  position: absolute;
  top: 50%;
  right: 142px;

  margin-top: -71px;

  transform: translateY(-50%);
}

.place__decore span {
  position: absolute;

  border-radius: 50%;
}

.place__decore span:nth-child(1) {
  animation: preview-ellips 6s infinite;
}

.place__decore span:nth-child(2) {
  animation: preview-ellips 6s infinite;

  animation-delay: 2s;
}

.place__decore span:nth-child(3) {
  animation: preview-ellips 6s infinite;

  animation-delay: 4s;
}

.place__texts {
  position: relative;

  display: grid;

  padding: 30px 24px;

  border-radius: 6px;

  background: #fff;
  box-shadow: 0 0 14px rgba(0, 0, 0, .25);

  line-height: 180%;
}

.place__texts b {
  font-weight: 600;
}

.place__text-top {
  margin: 0;
  margin-bottom: 20px;

  color: #202020;

  font-size: 20px;
}

.place__text {
  margin: 0;
}

.place__wrap-bottom {
  position: relative;

  display: grid;
}

.place__wrap-bottom::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;

  width: 778px;
  height: 615px;
  margin-top: -45px;
  margin-left: 420px;

  background-image: url("../img/place-dc-vagon.png");
  background-repeat: no-repeat;

  transform: translate(-50%, -50%);
}

.place__content-bottom {
  position: relative;

  max-width: 704px;

  color: #f8f1eb;

  line-height: 180%;
}

.place__content-bottom p {
  margin: 0;
  margin-bottom: 20px;
}

.place__content-bottom b {
  font-weight: 600;
}

.place__content-bottom .place__semi-text {
  max-width: 498px;
  margin-bottom: 8px;

  font-weight: 600;
}

.place__content-bottom .place__last-text {
  margin: 0;

  line-height: 47px;
}

.place__content-bottom .place__last-text span {
  font-weight: 600;
  font-size: 26px;
}
