.crumbs {
  position: absolute;
  z-index: 5;

  width: 100%;
  padding-top: 20px;

  font-size: 14px;
  line-height: 180%;
}

.crumbs__container {
  display: grid;
  grid-auto-flow: column;
  grid-column-gap: 20px;
  justify-content: start;
}

.crumbs a {
  position: relative;

  color: #433f38.5;

  text-decoration: none;
}

.crumbs a::before {
  content: "-";
  position: absolute;
  top: 0;
  right: -12px;

  align-self: center;

  margin-top: -1px;

  color: unset;

  pointer-events: none;
}
