.footer {
  padding-bottom: 15px;

  background-color: #001824;
}

.footer__container {
  display: grid;
}

@media (max-width: 1024px) {
  .footer__container {
    grid-row-gap: 40px;
  }
}

.footer__top-wrap {
  display: grid;
  grid-template-columns: minmax(max-content, 189px) minmax(max-content, 500px) max-content;
  grid-column-gap: 10px;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 1024px) {
  .footer__top-wrap {
    grid-template-columns: minmax(80px, max-content) max-content;
  }
}

@media (max-width: 480px) {
  .footer__top-wrap {
    grid-row-gap: 20px;
  }
}

.footer__logo {
  justify-self: start;

  padding-top: 7px;
  padding-bottom: 20px;
}

.footer__list {
  display: grid;
  grid-template-columns: repeat(4, max-content);
  grid-column-gap: 10px;
  justify-content: space-between;

  width: 100%;

  margin: 0;
  padding: 0;

  list-style: none;
}

@media (max-width: 1024px) {
  .footer__list {
    grid-row-start: 2;
    grid-column: 1/-1;
    justify-self: center;

    max-width: 500px;
    margin-top: -10px;
  }
}

@media (max-width: 480px) {
  .footer__list {
    grid-template-columns: 1fr;
    grid-row-gap: 24px;
    justify-items: center;

    margin-top: 0;
  }
}

.footer__link {
  color: #f8f1eb;

  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  text-decoration: none;

  transition: .2s;
}

.footer__link:hover,
.footer__link:focus {
  color: #ecbe4a;
}

.footer__contact-wrap {
  display: grid;
  grid-row-gap: 10px;
  justify-items: end;
}

.footer__contact {
  position: relative;

  display: grid;
  grid-template-columns: max-content max-content;
  grid-column-gap: 14px;
  align-items: center;

  color: #f8f1eb;

  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  text-decoration: none;

  transition: .2s;
}

.footer__contact:hover,
.footer__contact:focus {
  color: #ecbe4a;
}

.footer__contact::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -6px;

  width: 28px;
  height: 28px;

  background-image: url("../img/ellipse-small.svg");
  background-repeat: no-repeat;

  animation: 2s linear 0s normal none infinite running ellipce-small;
}

.footer__contact-icon {
  color: #f8f1eb;
}

.footer__bottom-wrap {
  display: grid;
  grid-template-columns: max-content minmax(max-content, 440px) minmax(max-content, 267px);
  align-items: center;
  justify-content: space-between;

  column-gap: 10px;
}

@media (max-width: 1024px) {
  .footer__bottom-wrap {
    grid-template-columns: max-content max-content;
    grid-row-gap: 10px;
  }
}

@media (max-width: 480px) {
  .footer__bottom-wrap {
    grid-template-columns: 1fr;
    grid-row-gap: 20px;
    justify-items: center;

    text-align: center;
  }
}

.footer__copyright {
  margin: 0;

  color: rgba(248, 241, 235, .5);

  font-size: 13px;
  line-height: 18px;
}

.footer__docs-wrap {
  display: grid;
  grid-template-columns: max-content max-content;
  grid-column-gap: 10px;
  justify-content: space-between;

  width: 100%;
}

@media (max-width: 1024px) {
  .footer__docs-wrap {
    grid-row-start: 1;
    grid-column: 1/-1;
    justify-self: center;

    max-width: 500px;
  }
}

@media (max-width: 480px) {
  .footer__docs-wrap {
    grid-template-columns: 1fr;
    grid-row-gap: 10px;
  }
}

.footer__doc {
  color: rgba(248, 241, 235, .5);

  font-size: 13px;
  line-height: 150%;
  text-decoration: none;

  transition: .2s;
}

.footer__doc:hover,
.footer__doc:focus {
  color: rgba(248, 241, 235, .8);
}

.footer__vyatka {
  justify-self: end;

  color: rgba(248, 241, 235, .6);

  font-size: 14px;
  line-height: 19px;
  text-decoration: none;
}

@media (max-width: 480px) {
  .footer__vyatka {
    justify-self: center;
  }
}

.footer__vyatka span {
  color: rgba(248, 241, 235, .9);
}
