.recall {
  position: relative;

  padding-top: 100px;
  padding-bottom: 120px;
}

@media (max-width: 480px) {
  .recall {
    padding-bottom: 60px;
  }
}

.recall::before {
  content: "";
  position: absolute;
  top: -200px;
  left: 0;

  width: 100%;
  height: 472px;

  background-image: url("../img/seo-index-top.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  pointer-events: none;
}

@media (max-width: 768px) {
  .recall::before {
    top: -100px;

    height: 372px;
  }
}

@media (max-width: 480px) {
  .recall::before {
    top: -28px;

    height: 300px;
  }
}

.recall::after {
  content: "";
  position: absolute;
  top: 176px;
  left: 0;

  width: 100%;
  height: 927px;

  background-image: url("../img/seo-bg.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  pointer-events: none;
}

.recall__decore::before {
  content: "";
  position: absolute;
  top: -300px;
  left: -200px;

  width: 379px;
  height: 234px;

  background-image: url("../img/index-dc-05.png");
  background-repeat: no-repeat;

  transform: rotate(20deg);
}

@media (max-width: 1200px) {
  .recall__decore::before {
    display: none;
  }
}

.recall__container {
  position: relative;

  display: grid;
}

.recall__container::before {
  content: "";
  position: absolute;
  top: 50px;
  left: 50%;

  width: 334px;
  height: 211px;

  margin-left: -505px;

  background-image: url("../img/recall-dc-left.png");
  background-repeat: no-repeat;

  transform: translateX(-50%);
}

.recall__container::after {
  content: "";
  position: absolute;
  top: -210px;
  left: 50%;

  width: 603px;
  height: 511px;

  margin-left: 340px;

  background-image: url("../img/recall-dc-right.png");
  background-repeat: no-repeat;

  transform: translateX(-50%) rotate(9deg);
}

.recall__wrap {
  position: relative;
  z-index: 1;

  display: grid;

  justify-self: center;

  width: 100%;
  max-width: 848px;
  padding: 16px;
  border-radius: 6px;

  background-color: #fcfcfc;
  box-shadow: 0 0 14px rgba(90, 66, 4, .25);
}

@media (max-width: 612px) {
  .recall__wrap {
    width: calc(100% + 40px);
  }
}

.recall__form {
  display: grid;
  grid-template-columns: 420px 240px;
  grid-column-gap: 20px;
  align-items: start;

  padding: 35px 40px 16px;
  border-radius: 6px;

  background: #fff;
  box-shadow: 0 0 14px rgba(90, 66, 4, .25);
}

@media (max-width: 832px) {
  .recall__form {
    grid-template-columns: minmax(200px, 1fr) 240px;
  }
}

@media (max-width: 612px) {
  .recall__form {
    grid-template-columns: 1fr;

    padding: 20px;
  }
}

.recall__lead {
  grid-column: 1/-1;

  margin: 0;
  margin-bottom: 20px;

  font-size: 22px;
  font-family: var(--Ubuntu);
  line-height: 180%;
}

@media (max-width: 480px) {
  .recall__lead {
    font-size: 18px;
  }
}

@media (max-width: 380px) {
  .recall__lead {
    font-size: 14px;
  }
}

.recall__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 20px;

  margin: 0;
  padding: 0;

  list-style: none;
}

@media (max-width: 832px) {
  .recall__list {
    grid-template-columns: 1fr;
    grid-row-gap: 20px;
  }
}

@media (max-width: 612px) {
  .recall__list {
    margin-bottom: 20px;
  }
}

.recall__item {
  position: relative;
}

.recall__icon {
  position: absolute;
  top: 50%;
  left: 16px;

  color: #433f38;

  transition: .2s;

  transform: translateY(-50%);
}

.recall__input {
  width: 100%;
  padding: 6px 16px 6px 38px;
  border: 1px solid #e9e9e9;
  border-radius: 6px;

  color: #433f38;

  font-size: 15px;
  line-height: 180%;
  letter-spacing: .05em;

  transition: .2s;
}

.recall__input:hover {
  border-color: #ecbe4a;
}

.recall__input:focus {
  border-color: #ecbe4a;

  background-color: #fafafa;
}

.recall__input:hover ~ .recall__icon,
.recall__input:focus ~ .recall__icon {
  color: #ecbe4a;
}

.recall__input::placeholder {
  color: #a5a5a5;
}

.recall__submit {
  display: grid;
}

.recall__button {
  width: 100%;
  margin-bottom: 6px;
  padding: 9px 10px;

  font-size: 16px;
  line-height: 22px;
  text-align: center;
}

@media (max-width: 612px) {
  .recall__button {
    margin-bottom: 12px;
  }
}

.recall__consent {
  margin: 0;

  color: #aaa;

  font-size: 12px;
  line-height: 140%;
}

.recall__consent a {
  color: unset;
}
