.header {
  position: relative;
  z-index: 10;

  width: 100%;

  background-color: #001824;
}

@media (max-width: 520px) {
  .header {
    padding-bottom: 20px;
  }
}

.page__body--main .header {
  position: absolute;

  background-color: transparent;
}

.header__container {
  display: grid;
  grid-template-columns: minmax(max-content, 212px) minmax(max-content, 514px) minmax(max-content, 269px) minmax(max-content, auto);
  grid-column-gap: 10px;
  align-items: center;
}

@media (max-width: 520px) {
  .header__container {
    grid-template-columns: minmax(80px, 133px) max-content 28px;
    justify-content: space-between;
  }
}

.header__logo {
  display: grid;
  justify-self: start;

  padding-top: 2px;
  padding-bottom: 26px;
}

.header__contact {
  position: relative;

  display: grid;
  grid-template-columns: max-content max-content;
  grid-column-gap: 26px;
  align-items: center;
  justify-self: start;

  color: #f8f1eb;

  font-weight: 600;
  font-style: normal;
  font-size: 20px;
  line-height: 27px;
  text-decoration: none;

  transition: .2s;
}

@media (max-width: 680px) {
  .header__contact {
    grid-template-columns: max-content;
  }
}

@media (max-width: 520px) {
  .header__contact {
    grid-row-start: 1;
    grid-column-start: 2;
  }
}

.header__contact:hover,
.header__contact:focus {
  color: #ecbe4a;
}

.header__contact::before {
  content: "";
  position: absolute;
  top: -1px;
  left: -6px;

  width: 28px;
  height: 28px;

  background-image: url("../img/ellipse-small.svg");
  background-repeat: no-repeat;

  animation: 2s linear 0s normal none infinite running ellipce-small;
}

@media (max-width: 680px) {
  .header__contact::before {
    top: -6px;
  }
}

.header__contact::after {
  content: "";
  position: absolute;
  top: -9px;
  left: -14px;

  width: 44px;
  height: 44px;

  background-image: url("../img/ellipse-big.svg");
  background-repeat: no-repeat;

  animation: 2s linear 0s normal none infinite running ellipce-big;
}

@media (max-width: 680px) {
  .header__contact::after {
    top: -14px;
  }
}

@media (max-width: 680px) {
  .header__contact span {
    display: none;
  }
}

.header__contact-icon {
  color: #f8f1eb;
}

.header__popup {
  width: 100%;

  max-width: 189px;
  padding: 9px 15px;
  border: 1px solid #f8f1eb;
  border-radius: 6px;

  background-color: transparent;
  color: #f8f1eb;

  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  text-align: center;

  cursor: pointer;
}

@media (max-width: 520px) {
  .header__popup {
    grid-column: 1/-1;
    justify-self: center;

    margin-top: -10px;
  }
}

.header__popup:hover,
.header__popup:focus {
  padding: 10px 16px;
  border: none;

  background: linear-gradient(180deg, #ecbe4a 0%, #cb9817 100%);
  color: #5a4204;
}
