.products {
  position: relative;

  padding-bottom: 120px;

  background-color: #001824;
}

@media (max-width: 768px) {
  .products {
    padding-bottom: 80px;
  }
}

@media (max-width: 480px) {
  .products {
    padding-bottom: 60px;
  }
}

.products::before {
  content: "";
  position: absolute;
  top: -246px;
  left: 0;

  width: 100%;
  height: 472px;

  background-image: url("../img/product-top-dc.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

@media (max-width: 480px) {
  .products::before {
    top: -146px;

    height: 372px;
  }
}

.products::after {
  content: "";
  position: absolute;
  top: 216px;
  left: 0;

  width: 100%;
  height: 856px;

  background-image: url("../img/product-bg.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.products__decore::before {
  content: "";
  position: absolute;
  top: -250px;
  right: -300px;
  z-index: 1;

  width: 604px;
  height: 513px;

  background-image: url("../img/index-dc-02.png");
  background-repeat: no-repeat;

  transform: rotate(-43deg);
}

@media (max-width: 1200px) {
  .products__decore::before {
    display: none;
  }
}

.products__decore::after {
  content: "";
  position: absolute;
  top: 450px;
  left: -150px;
  z-index: 2;

  width: 394px;
  height: 394px;

  background-image: url("../img/index-dc-03.png");
  background-repeat: no-repeat;
}

@media (max-width: 1200px) {
  .products__decore::after {
    display: none;
  }
}

.products__container {
  position: relative;
  z-index: 1;

  display: grid;
}

.products__title {
  margin: 0;
  margin-bottom: 80px;

  color: #f8f1eb;

  font-weight: 500;
  font-size: 36px;
  font-family: var(--Ubuntu);
  line-height: 120%;
  text-align: center;
}

@media (max-width: 768px) {
  .products__title {
    margin-bottom: 40px;
  }
}

.products__buttons {
  display: grid;
  grid-template-columns: minmax(54px, 92px) 1fr minmax(54px, 92px);
  grid-row-start: 2;
  grid-column: 1/-1;
  grid-column-gap: 10px;
}

@media (max-width: 920px) {
  .products__buttons {
    grid-template-columns: 54px 54px;
    grid-column-gap: 40px;
    justify-self: end;

    margin-bottom: 40px;
  }
}

.products__button {
  position: relative;
  top: 130px;
  z-index: 1;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 54px;
  height: 54px;
  padding: 0;
  border: none;
  border-radius: 50%;

  background-color: rgba(248, 241, 235, .3);
  color: #f8f1eb;
  box-shadow: 0 0 14px rgba(0, 0, 0, .06);

  cursor: pointer;

  transition: .2s;
}

@media (max-width: 920px) {
  .products__button {
    top: 0;
  }
}

.products__button:hover,
.products__button:focus {
  background-color: #f8f1eb;
  color: #433f38;
  box-shadow: 0 0 14px rgba(0, 0, 0, .15);
}

.products__button-title {
  position: absolute;
  bottom: -35px;
  left: 50%;

  width: 180px;

  color: rgba(248, 241, 235, .3);

  font-size: 16px;
  line-height: 120%;
  text-align: center;

  transition: .2s;

  transform: translateX(-50%);
}

@media (max-width: 920px) {
  .products__button-title {
    display: none;
  }
}

.products__button:hover .products__button-title,
.products__button:focus .products__button-title {
  color: rgba(248, 241, 235, .8);
}

.products__button--prev {
  grid-column: 1/2;
  justify-self: end;
}

.products__button--next {
  grid-column: 3/4;
  justify-self: start;
}

@media (max-width: 920px) {
  .products__button--next {
    grid-column: 2/3;
  }
}

.products__icon {
  color: unset;

  pointer-events: none;
}

.products__button--next .products__icon {
  transform: rotate(-180deg);
}

.products__slider {
  display: grid;
  grid-template-columns: minmax(54px, 92px) 1fr minmax(54px, 92px);
  grid-row-start: 2;
  grid-column: 1/-1;
  overflow: hidden;
}

@media (max-width: 920px) {
  .products__slider {
    grid-template-columns: 1fr;
    grid-row-start: 3;
  }
}

.products__list {
  display: grid;
  grid-template-columns: repeat(100, 100%);
  grid-column: 1/4;

  grid-column-gap: 20px;
  align-items: start;

  width: 100%;
  margin: 0;
  margin-bottom: 30px;
  padding: 0;

  list-style: none;
}

.products__item {
  display: grid;

  max-width: 100%;
}

.products__item h3 {
  margin: 0;
  margin-bottom: 30px;

  color: #f8f1eb;

  font-weight: 500;
  font-size: 52px;
  font-family: var(--Ubuntu);
  line-height: 120%;
  text-align: center;
}

@media (max-width: 480px) {
  .products__item h3 {
    font-size: 36px;
  }
}

.products__image {
  position: relative;

  display: grid;
  grid-row-start: 1;
  justify-content: center;

  margin-bottom: 12px;
}

.products__image img:first-child {
  z-index: 1;

  width: 254px;
  height: 229px;
}

.products__image img:last-child {
  position: absolute;
  top: -117px;
  left: 50%;

  width: 497px;
  min-width: 497px;
  height: 420px;
  min-height: 420px;

  margin-left: 10px;

  pointer-events: none;

  transform: translateX(-50%);
}

.products__wrap {
  display: grid;
  grid-template-columns: 212px auto;
  grid-column-gap: 14px;
  align-items: start;
  justify-self: center;

  margin-bottom: 30px;
}

@media (max-width: 768px) {
  .products__wrap {
    grid-template-columns: 1fr;

    width: 100%;
  }
}

.products__table {
  display: grid;
  grid-row-gap: 16px;

  width: 100%;

  margin: 0;

  text-align: right;
}

@media (max-width: 768px) {
  .products__table {
    justify-content: center;

    margin-bottom: 20px;

    text-align: center;
  }
}

.products__table div {
  display: grid;
  grid-row-gap: 12px;

  max-width: 212px;
}

.products__table dt {
  color: #f8f1eb;

  font-weight: 600;
  line-height: 120%;
}

.products__table dd {
  margin: 0;

  color: rgba(248, 241, 235, .7);

  font-size: 14px;
  line-height: 120%;
}

.products__text {
  position: relative;

  display: grid;

  max-width: 522px;

  padding-left: 14px;
}

@media (max-width: 768px) {
  .products__text {
    max-width: 100%;
    padding: 0;

    text-align: center;
  }
}

.products__text::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;

  width: 1px;
  height: 100%;

  background-color: #f8f1eb.2;
}

@media (max-width: 768px) {
  .products__text::before {
    display: none;
  }
}

.products__text p {
  margin: 0;

  color: #f8f1eb;

  line-height: 180%;
}

.products__subtext {
  justify-self: center;

  max-width: 642px;
  margin: 0;
  margin-bottom: 30px;

  color: rgba(248, 241, 235, .7);

  font-size: 15px;
  line-height: 140%;
  text-align: center;
}

.products__popup {
  justify-self: center;

  padding: 9px 20px;
}

@media (max-width: 480px) {
  .products__popup {
    font-size: 14px;
  }
}

.products__dots {
  display: grid;
  grid-column: 1/-1;
  grid-auto-flow: column;
  grid-gap: 6px;
  justify-self: center;

  max-width: 100px;
}

.swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  border-radius: 50%;

  background-color: #868686;

  cursor: pointer;

  transition: .2s;
}

.swiper-pagination-bullet-active {
  background-color: #f8f1eb;
}
