*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Body */
.page__body {
  position: relative;

  display: grid;
  grid-template-rows:
    auto
    1fr
    auto;

  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  padding: 0;

  background-color: #f8f1eb;
  color: #433f38;

  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  font-family: var(--OpenSans);
  line-height: 1;
  overflow-anchor: none;
}

.page__body--main {
  grid-template-rows:
    1fr
    auto;
}

.page__body--inert {
  position: relative;
}

.page__body--inert::before {
  content: "";
  position: absolute;
  z-index: 4;

  width: 100%;
  height: 100%;

  background-color: #332e2e.8;
}

/* Image */
img {
  max-width: 100%;
  height: auto;
}

/* Picture */
picture {
  display: flex;
  align-items: center;
}

/* Input */
input[type="number"] {
  appearance: textfield;
}

textarea {
  resize: none;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  display: grid;

  box-shadow: transsparrent;

  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: #ebebeb;
}

/* Focus */
.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}
