.preview {
  position: relative;

  padding: 220px 0 260px;

  background-color: #433f38;
}

@media (max-width: 1024px) {
  .preview {
    padding-bottom: 180px;
  }
}

@media (max-width: 768px) {
  .preview {
    padding-bottom: 160px;
  }
}

@media (max-width: 768px) {
  .preview {
    padding-bottom: 100px;
  }
}

.preview::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 1;

  width: 100%;
  height: 100%;
  min-height: 939px;

  background-image: url("../img/preview-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  transform: translateX(-50%);
}

.preview__container {
  position: relative;
  z-index: 2;

  display: grid;
  grid-template-columns: minmax(280px, 841px) 150px;
}

@media (max-width: 560px) {
  .preview__container {
    grid-template-columns: 1fr;
  }
}

.preview__content-wrap {
  display: grid;
  grid-row-start: 1;
  grid-column: 1/-1;
}

.preview__lead {
  max-width: 780px;
  margin: 0;
  margin-bottom: 20px;

  color: #f8f1eb;

  font-weight: bold;
  font-size: 54px;
  font-family: var(--Ubuntu);
  line-height: 120%;
}

@media (max-width: 600px) {
  .preview__lead {
    font-size: 44px;
  }
}

@media (max-width: 480px) {
  .preview__lead {
    font-size: 32px;
  }
}

.preview__text {
  max-width: 512px;
  margin: 0;
  margin-bottom: 30px;

  color: #f8f1eb;

  font-size: 20px;
  line-height: 180%;
}

@media (max-width: 600px) {
  .preview__text {
    max-width: 420px;

    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .preview__text {
    font-size: 14px;
  }
}

.preview__button {
  justify-self: start;

  padding: 9px 18px;
}

.button {
  border: none;
  border-radius: 6px;

  background: linear-gradient(180deg, #ecbe4a 0%, #cb9817 100%);
  color: #5a4204;
  box-shadow: 0 0 15px rgba(203, 152, 23, .3), 0 2px 0 #ad7e07;

  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  text-align: center;

  cursor: pointer;

  transition: .2s;
}

.button:hover,
.button:focus {
  background: linear-gradient(180deg, #eaba42 0%, #cb950e 100%);
  box-shadow: 0 0 15px rgba(203, 152, 23, .7), 0 2px 0 #ad7e07;
}

.preview__decore-wrap {
  display: grid;
  grid-row-start: 1;
  grid-column-start: 2;
}

@media (max-width: 560px) {
  .preview__decore-wrap {
    display: none;
  }
}

.preview__decore {
  position: relative;
  bottom: -39px;

  display: grid;
  align-items: center;
  align-self: end;
  justify-content: center;

  width: 150px;
  height: 150px;
  border-radius: 50%;

  background: rgba(200, 238, 93, .9);
  box-shadow: 0 0 14px rgba(0, 0, 0, .25);
}

.preview__decore::before {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 0;
  z-index: 1;

  width: 63px;
  height: 51px;

  background-image: url("../img/preview-dc.png");
  background-repeat: no-repeat;

  transform: rotate(6deg);
}

.preview__decore p {
  max-width: 127px;
  margin: 0;

  color: #433f38;

  font-size: 14px;
  line-height: 160%;
  text-align: center;
}

.preview__decore span {
  position: absolute;

  border-radius: 50%;
}

.preview__decore span:nth-child(2) {
  animation: preview-ellips 6s infinite;
}

.preview__decore span:nth-child(3) {
  animation: preview-ellips 6s infinite;

  animation-delay: 2s;
}

.preview__decore span:nth-child(4) {
  animation: preview-ellips 6s infinite;

  animation-delay: 4s;
}
