.popup {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;

  width: 100%;
  height: 100%;

  background-color: #332e2e.8;

  opacity: 0;

  transition: .2s opacity;
}

.popup__wrap {
  position: fixed;
  top: 50vh;
  left: 50%;

  overflow-y: auto;

  width: 100%;
  max-width: 538px;
  max-height: 100%;

  padding: 12px;

  border-radius: 6px;

  background: #fcfcfc;
  box-shadow: 0 0 14px rgba(90, 66, 4, .25);

  transform: translate(-50%, -50%);
}

.popup__icon-close {
  color: currentColor;

  pointer-events: none;

  transition: .2s;
}

.popup__close {
  position: absolute;
  top: 28px;
  right: 28px;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 20px;
  height: 20px;
  padding: 0;
  border: none;

  background-color: transparent;
  color: #c8c4c4;

  cursor: pointer;
}

.popup__close:hover,
.popup__close:focus {
  color: #433f38;
}

.popup__before {
  display: grid;
  justify-items: center;

  padding: 38px 10px 30px;

  border-radius: 6px;

  background: #fff;
  box-shadow: 0 0 14px rgba(90, 66, 4, .25);
}

.popup__before h2 {
  margin: 0;

  font-weight: 500;
  font-size: 24px;
  font-family: var(--Ubuntu);
  line-height: 28px;
  text-align: center;
}

.popup__before p {
  max-width: 294px;

  color: #433f38.8;

  font-size: 16px;
  line-height: 125%;
  text-align: center;
}

.popup__form {
  width: 100%;
  max-width: 240px;
}

.popup__list {
  display: grid;
  grid-row-gap: 16px;

  margin: 0;
  margin-bottom: 50px;
  padding: 0;

  list-style: none;
}

.popup__item {
  position: relative;
}

.popup__icon {
  position: absolute;
  top: 50%;
  left: 16px;

  color: #a5a5a5;

  transition: .2s;

  transform: translateY(-50%);
}

.popup__input {
  width: 100%;
  padding: 6px 16px 6px 38px;
  border: 1px solid #e9e9e9;
  border-radius: 6px;

  color: #433f38;

  font-size: 15px;
  line-height: 180%;
  letter-spacing: .05em;

  transition: .2s;
}

.popup__input:hover {
  border-color: #ecbe4a;
}

.popup__input:focus {
  border-color: #ecbe4a;

  background-color: #fafafa;
}

.popup__input:hover ~ .popup__icon,
.popup__input:focus ~ .popup__icon {
  color: #ecbe4a;
}

.popup__input::placeholder {
  color: #a5a5a5;
}

.popup__input--message {
  padding: 6px 16px;
}

.popup__submit {
  width: 100%;
  margin-bottom: 6px;
  padding: 9px;
}

.popup__before .popup__consent {
  max-width: 226px;
  margin: 0 auto;

  color: #aaa;

  font-size: 12px;
  line-height: 140%;
  text-align: center;
}

.popup__before .popup__consent a {
  color: unset;
}

.popup__after {
  display: grid;
  justify-items: center;

  padding: 108px 10px 70px;
  border-radius: 6px;

  background: #fff;
  box-shadow: 0 0 14px rgba(90, 66, 4, .25);
}

.popup__after h2 {
  width: 100%;
  max-width: 247px;
  margin: 0;
  margin-bottom: 16px;

  font-weight: 500;
  font-size: 24px;
  font-family: var(--Ubuntu);
  line-height: 28px;
  text-align: center;
}

.popup__after p {
  width: 100%;
  max-width: 294px;
  margin: 0;
  margin-bottom: 70px;

  color: #434343.8;

  line-height: 125%;
  text-align: center;
}

.popup__thanks {
  width: 100%;
  max-width: 240px;
  padding: 9px;
}
