.about {
  position: relative;

  padding: 170px 0 210px;

  background-image: url("../img/about-index-bg.png");
  background-position: top center;

  background-size: 100% 635px;
  background-repeat: no-repeat;
}

@media (max-width: 1024px) {
  .about {
    padding-top: 130px;
  }
}

@media (max-width: 768px) {
  .about {
    padding-top: 110px;
  }
}

@media (max-width: 480px) {
  .about {
    padding-top: 80px;
    padding-bottom: 140px;
  }
}

.about::before {
  content: "";
  position: absolute;
  top: -181px;
  left: 0;
  z-index: 2;

  width: 100%;
  height: 472px;

  background-image: url("../img/about-top-dc.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

@media (max-width: 1024px) {
  .about::before {
    top: -101px;

    height: 392px;
  }
}

@media (max-width: 768px) {
  .about::before {
    top: -81px;

    height: 372px;
  }
}

@media (max-width: 768px) {
  .about::before {
    top: -81px;

    height: 372px;
  }
}

.about::after {
  content: "";
  position: absolute;
  top: 600px;
  right: -70px;
  z-index: 1;

  width: 224px;
  height: 231px;

  background-image: url("../img/index-dc-01.png");
  background-repeat: no-repeat;
}

@media (max-width: 1200px) {
  .about::after {
    display: none;
  }
}

.about__container {
  display: grid;
  grid-template-columns: 1fr 704px;
  grid-column-gap: 10px;
}

@media (max-width: 768px) {
  .about__container {
    grid-template-columns: 1fr;
  }
}

.about__container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;

  width: 827px;
  height: 700px;

  background-image: url("../img/about-dc.png");
  background-repeat: no-repeat;
}

@media (max-width: 1800px) {
  .about__container::before {
    left: 50%;

    margin-left: -490px;

    transform: translateX(-50%);
  }
}

@media (max-width: 1280px) {
  .about__container::before {
    transform: translateX(-70%);
  }
}

@media (max-width: 880px) {
  .about__container::before {
    display: none;
  }
}

.about__top-wrap {
  position: relative;
  z-index: 2;

  display: grid;
  grid-column: 2/3;

  margin-bottom: 145px;
}

@media (max-width: 880px) {
  .about__top-wrap {
    grid-column: 1/-1;
  }
}

@media (max-width: 768px) {
  .about__top-wrap {
    margin-bottom: 80px;
  }
}

.about__title {
  margin: 0;
  margin-bottom: 30px;

  font-weight: 500;
  font-size: 36px;
  font-family: var(--Ubuntu);
  line-height: 120%;
}

.about__text {
  margin: 0;
  margin-bottom: 30px;

  font-size: 16px;
  line-height: 180%;
}

.about__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 10px;

  margin: 0;
  margin-bottom: 60px;
  padding: 0;

  list-style: none;
}

@media (max-width: 768px) {
  .about__list {
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 30px;
  }
}

@media (max-width: 768px) {
  .about__list {
    grid-template-columns: 1fr;
  }
}

.about__item {
  position: relative;

  padding-left: 15px;

  line-height: 180%;
}

.about__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;

  width: 1px;
  height: 100%;

  background-color: #433f38.3;
}

.about__item p {
  margin: 0;

  color: #433f38.8;
}

.about__item span b {
  font-weight: 600;
  font-size: 32px;
}

.about__link {
  display: grid;
  grid-template-columns: max-content max-content;
  grid-column-gap: 8px;
  align-items: center;
  justify-self: start;

  padding: 9px 24px;
  border: 1px solid #cb9817;
  border-radius: 6px;

  color: #cb9817;

  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  text-decoration: none;
}

.about__link:hover,
.about__link:focus {
  background: linear-gradient(180deg, #eaba42 0%, #cb950e 100%), #92c500;
  color: #453202;
}

.about__link-icon {
  position: relative;
  top: 1px;

  color: unset;
}

.about__bottom-wrap {
  display: grid;
  grid-template-columns: minmax(280px, 602px) minmax(320px, 1fr);
  grid-column: 1/-1;
  grid-column-gap: 10px;
  align-items: start;
}

@media (max-width: 768px) {
  .about__bottom-wrap {
    grid-template-columns: 1fr;
    grid-row-gap: 30px;
  }
}

.about__bottom-wrap::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;

  width: 100vw;
  height: 790px;

  background-image: url("../img/about-index-bg2.png");
  background-size: cover;
  background-repeat: no-repeat;

  pointer-events: none;
}

.about__subtitle {
  margin: 0;
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 24px;
  font-family: var(--Ubuntu);
  line-height: 120%;
}

.about__text-wrap {
  position: relative;

  display: grid;
  grid-row-gap: 16px;
}

.about__text-wrap::before {
  content: "";
  position: absolute;
  top: 30px;
  left: 100px;

  width: 174px;
  height: 149px;

  background-image: url("../img/about-dc.svg");
  background-repeat: no-repeat;
}

.about__text-wrap p {
  margin: 0;

  line-height: 180%;
}

.about__right-wrap {
  display: grid;
}

.about__figure {
  display: grid;

  max-width: 398px;

  margin: 0;
}

@media (max-width: 768px) {
  .about__figure {
    justify-self: end;

    width: 100%;
    max-width: 385px;
  }
}

.about__figure-image {
  justify-self: end;
}

.about__figcaption {
  justify-self: start;

  width: 100%;
  max-width: 266px;
  margin-top: -72px;

  padding: 6px;
  border-radius: 6px;

  background: #fcfcfc;
  box-shadow: 0 0 14px rgba(90, 66, 4, .25);

  transform: skew(-10deg);
}

.about__figcaption div {
  display: grid;
  justify-items: center;

  padding: 20px;

  border-radius: 6px;

  background: #fff;
  box-shadow: 0 0 14px rgba(90, 66, 4, .25);
}

.about__figcaption div cite {
  margin-bottom: 12px;

  font-weight: 600;
  font-style: normal;
  line-height: 120%;

  transform: skew(10deg);
}

.about__figcaption div p {
  margin: 0;

  font-style: normal;
  line-height: 120%;

  transform: skew(10deg);
}

.about--page {
  padding-top: 120px;
  padding-bottom: 340px;

  background-image: url("../img/about-page-bg.png");
  background-size: 100% 635px;
}

@media (max-width: 1024px) {
  .about--page {
    padding-bottom: 260px;
  }
}

@media (max-width: 480px) {
  .about--page {
    padding-top: 80px;
    padding-bottom: 140px;
  }
}

.about--page::before {
  display: none;
}

.about--page::after {
  display: none;
}

.about--page .about__top-wrap {
  margin-bottom: 0;
}

.about__content-wrap {
  display: grid;

  grid-row-gap: 10px;
}

.about__content-wrap h3 {
  max-width: 370px;
  margin: 0;
  margin-bottom: 20px;

  color: #2d2a2a;

  font-weight: 500;
  font-size: 24px;
  font-family: var(--Ubuntu);
  line-height: 120%;
}

.about__content-wrap p {
  margin: 0;

  color: #2d2a2a.8;

  font-size: 16px;
  line-height: 180%;
}
