.nav {
  position: relative;

  display: grid;

  padding-right: 14px;
}

@media (max-width: 520px) {
  .nav {
    grid-column-start: 3;

    padding-right: 0;
  }
}

.nav__button {
  display: grid;
  justify-self: start;

  padding: 0;
  border: none;

  background-color: transparent;

  cursor: pointer;
}

@media (min-width: 1025px) {
  .nav__button {
    display: none;
  }
}

.nav__button-icon {
  color: #f8f1eb;

  pointer-events: none;

  transition: .2s;
}

.nav__button:hover .nav__button-icon,
.nav__button:focus .nav__button-icon {
  color: #ecbe4a;
}

.nav__list {
  display: grid;

  margin: 0;
  padding: 0;

  list-style: none;
}

@media (min-width: 1025px) {
  .nav__list {
    grid-template-columns: repeat(4, max-content);
    grid-gap: 10px;
    justify-content: space-between;
    justify-self: start;

    width: 100%;
    max-width: 434px;
  }
}

@media (max-width: 1024px) {
  .nav--closed .nav__list {
    display: none;
  }
}

@media (max-width: 1024px) {
  .nav--opened .nav__list {
    position: absolute;
    top: 67px;
    left: 0;

    overflow: hidden;

    width: 160px;

    border-radius: 0 0 6px 6px;

    background-color: #fcfcfc;
    box-shadow: 0 0 14px rgba(90, 66, 4, .25);

    animation: nav-open .2s;
  }
}

@media (max-width: 1024px) {
  .page__body--main .nav--opened .nav__list {
    border-radius: 6px;

    background-color: #fcfcfc.9;
  }
}

@media (max-width: 520px) {
  .nav--opened .nav__list {
    top: 119px;
    right: 0;
    left: auto;
  }
}

.nav__link {
  position: relative;

  display: grid;

  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  text-decoration: none;

  transition: .2s;
}

@media (min-width: 1025px) {
  .nav__link {
    color: #f8f1eb;
  }
}

@media (max-width: 1024px) {
  .nav__link {
    padding: 20px;

    color: #433f38;
  }
}

@media (max-width: 520px) {
  .nav__link {
    text-align: right;
  }
}

@media (min-width: 1025px) {
  .nav__link:hover,
  .nav__link:focus {
    color: #ecbe4a;
  }

  .nav__link:hover::before,
  .nav__link:focus::before {
    content: "";
    position: absolute;
    bottom: -4px;
    left: 0;

    height: 1px;

    background-color: #ecbe4a;

    pointer-events: none;

    animation: nav-link 2s;
  }
}

@media (max-width: 1024px) {
  .nav__link:hover,
  .nav__link:focus {
    background-color: #ecbe4a;
  }
}
